import axios from 'axios'
import { Dialog } from 'vant'
const service = axios.create({
  baseURL: '/api',
  timeout: 10000,
  withCredentials: true
})

service.interceptors.request.use(config => {
  const Authorization = localStorage.getItem('throw-token')
  if (Authorization) {
    config.headers = Object.assign(config.headers, { Authorization: 'Bearer ' + Authorization })
  }
  return config
})
service.interceptors.response.use(
  response => {
    const { data: { code, data, msg } } = response
    // 成功返回
    if (code === 1000) {
      return data
    }
    if (code === 30006 && data.group) {
      // 微信外已经报名  存储分流信息
      localStorage.setItem('pageId', data.group)
      localStorage.setItem('uniqFlag', data.uniqFlag)
    }
    if (code === 34001) {
      console.log(data.url)
    }
    // 失败提示 并返回失败结果
    /*
      30006 微信外购买过需要  弹框取公众号页面
      30009 微信内购买过    弹框去二维码页
      35001 礼盒落地页 没有库存
      34001 微信内未登录
      这些code 需要单独处理
     */
    if (![30006, 30009, 35001, 34001, -1, 39101, 36002, 30013, 30016, 30014, 30015, 39100, 39101, 39102, 39103, 39104, 39105, 39106].includes(code) && msg) {
      Dialog({ message: msg })
    }
    return Promise.reject(response.data)
  },
  error => {
    // Toast('请求失败', error)
    console.log(error)
    return Promise.reject(error)
  })
export default service
