import promiseWx from '@/libs/promiseWx'
import { getSearch } from '@/utils'
const classInfo = localStorage.getItem('classInfo')
const className = localStorage.getItem('className')
export default {
  namespaced: true,
  state: {
    userInfo: null,
    wxConfig: null,
    wm_click_id: sessionStorage.getItem('wm_click_id'),
    classInfo: classInfo ? JSON.parse(classInfo) : {},
    className: classInfo ? JSON.parse(className) : {}
  },
  mutations: {
    SET_USER (state, payload) {
      state.userInfo = payload
    },
    SET_WX_CONFIG (state, payload) {
      state.wxConfig = payload
    },
    SET_CLICKID (state, payload) {
      state.wm_click_id = payload
    },
    SET_CLASS_INFO (state, payload) {
      state.classInfo = payload
    },
    SET_CLASS_NAME (state, payload) {
      state.className = payload
    }
  },
  actions: {
    /**
     * @description 获取微信config
     */
    async getWxConfig ({ commit }) {
      try {
        // 不知道为什么
        // encodeURIComponent(location.href.split('#')[0])
        // 用这个会报签名错误
        const wxConfig = await this.$apis.user.getWxConfig({
          url: location.href
        })
        await promiseWx.mount({
          appId: wxConfig.appId,
          nonceStr: wxConfig.nonceStr,
          signature: wxConfig.signature,
          timestamp: String(wxConfig.timestamp)
        })
        commit('SET_WX_CONFIG', wxConfig)
      } catch (error) {
        console.error(error)
      }
    },
    /**
     * @description 获取用户信息
     */
    async getUserInfo ({ commit }) {
      try {
        const userInfo = await this.$apis.user.getUserInfo()
        commit('SET_USER', userInfo)
      } catch (error) {
        if (error.code === 34001) { // 未授权、跳转登录
          const { url } = error.data
          // 记录之前获取用户信息跳转过
          localStorage.setItem('wxauth', true)
          window.location.href = url + encodeURIComponent(location.href)
        }
        throw error
      }
    },
    /**
     * @Date: 2021-01-04 18:28:32
     * @author: zjs
     * @description: 换取token
     */
    async getToken ({ commit }, model) {
      try {
        const token = getSearch('token')
        const userInfo = await this.$apis.user.userLogin({ token, ...model })
        localStorage.removeItem('wxauth')
        if (token) {
          localStorage.setItem('throw-token', userInfo.token)
        }
      } catch (error) {
        if (error.code === 34001) { // 未授权、跳转登录
          const { url } = error.data
          // 记录之前获取用户信息跳转过
          localStorage.removeItem('wxauth')
          window.location.href = url + encodeURIComponent(location.href)
        }
      }
    },
    async getWmClikId ({ commit }) {
      const data = await this.$apis.user.newClickId()
      sessionStorage.setItem('wm_click_id', data.wm_click_id)
      commit('SET_CLICKID', data.wm_click_id)
      return data.wm_click_id
    }
  }
}
