import { setDocumentTitle } from '@/utils'
import Store from '@/store'

export default router => {
  router.beforeEach(async (to, from, next) => {
    const { origin, search } = location
    const path = `${origin}/customer/${search}#${from.path}`
    Store.$log.update({ pre_page: path })
    // 防止设置标题重复请求
    if (window.location.href.indexOf('replace_title') !== -1) {
      return
    }
    // 设置埋点访问渠道号
    const { type } = to.query
    Store.$log.update({
      visit_channel: Number(type) || ''
    })

    // 设置标题
    const title = to.meta.title || '微淼'
    setDocumentTitle(title)
    console.log(to.meta.appid, !to.query.appid)
    if (to.meta.appid && !to.query.appid) {
      return next({
        name: to.name,
        query: {
          ...to.query,
          appid: to.meta.appid
        }
      })
    }
    next()
  })
  router.afterEach((to, from) => {

  })
}
