import {
  ActionSheet,
  Tabs,
  Tab,
  Form,
  Field,
  Swipe,
  SwipeItem,
  Button,
  Toast,
  Dialog,
  List,
  Popup,
  RadioGroup,
  Radio,
  CountDown,
  Loading,
  DatetimePicker
} from 'vant'
Dialog.setDefaultOptions({
  confirmButtonText: '确定'
})
export default Vue => {
  Vue
    .use(ActionSheet)
    .use(Tabs)
    .use(Tab)
    .use(Form)
    .use(Field)
    .use(Swipe)
    .use(SwipeItem)
    .use(Button)
    .use(Toast)
    .use(Dialog)
    .use(List)
    .use(Popup)
    .use(RadioGroup)
    .use(Radio)
    .use(CountDown)
    .use(Loading)
    .use(DatetimePicker)
}
