export default {
  namespaced: true,
  state: {
    backDialogMark: false, // 挽留弹窗标记
    abData: {}
  },
  mutations: {
    SET_BACK_DIALOG_MARK (state, payload) {
      state.backDialogMark = payload
    },
    SET_ABDATA (state, payload) {
      state.abData = payload
    }
  },
  actions: {
    async getAbData ({ commit, state }, params) {
      // eslint-disable-next-line camelcase
      const ab_key = ['Throw', params.domain, params.abKey].join('_')
      // eslint-disable-next-line camelcase
      if (ab_key === state.abData.ab_key) {
        return state.abData
      }
      try {
        const data = await this.$apis.userOperator.getGroup(params)
        const abData = {
          ab_key,
          ab_group: data.group,
          ab_uniq_user: data.uniqFlag
        }
        commit('SET_ABDATA', abData)
        this.$log.update({
          alternate: JSON.stringify(abData)
        })
      } catch (error) {
        console.log(error)
      }
      return state.abData
    }
  }
}
