import WMStatistics from '@wm/wm-burying-point'

const servicesName = 'toufang'
const options = {
  env: process.env.NODE_ENV,
  mode: 'img'
}
const logs = new WMStatistics(servicesName, options)
// update方法
const update = logs.update
logs.update = function (body, header) {
  const lastOpt = this.statistics.options.body.alternate
  // 如果都含有alternate  TODO 需要保证body.alternate是正确的json格式
  if (body.alternate && lastOpt) {
    console.log(lastOpt)
    body.alternate = JSON.stringify({ ...JSON.parse(lastOpt), ...JSON.parse(body.alternate) })
  }
  update.bind(this)(body, header)
}
export const registerLog = (Vue, Store) => {
  Vue.use(logs)
  Store.$log = logs
}

export default logs
