import service from './service'
import { getSearch } from '@/utils/index'
export default {
  user: { // 用户相关
    // 获取用户信息
    getUserInfo: (params) => {
      params = { ...params, ...getSearch() }
      return service.get('/throw/m/wxauth/user', { params })
    },
    // 用户登录
    userLogin: params => service.get('/throw/wxauth/login', { params }),
    // 获取微信配置
    getWxConfig: params => {
      const query = getSearch()
      if (query.appid === 'changqing-shoushen') {
        params.doman = 'changqing'
      }
      return service.get('/throw/wxauth/package', { params })
    },
    // 生成wmclick
    newClickId: (params) => service.get('/throw/common/newClickId', { params }),
    // 获取指定文件
    getClassFile: (params) => {
      params = { ...params, ...getSearch() }
      return service.get('throw/m/class/getClassFile', { params })
    },
    // 获取长轻七元课程优惠券状态
    getMemberHold: (params) => {
      params = { ...params, ...getSearch() }
      return service.get('/throw/m/coupon/getMemberHold', { params })
    },
    // 领取优惠券 /api/throw/m/coupon/issueCoupon
    issueCoupon: (params) => {
      params = { ...params, ...getSearch() }
      return service.get('/throw/m/coupon/issueCoupon', { params })
    },
    // 分班
    fenban: params => service.post('/throw/m/miniProgram/fenban', params),
    // 获取各个商品标题 throw/common/getProductNameList
    getProductNameList: params => service.post('/throw/common/getProductNameList', params)

  },
  baiduPcPayOrder: { // 百度Pc 跳H5落地页
    // 绑定手机
    bindPhone: params => service.post('/throw/m/common/bindUserIdMobile', params),
    // 获取分班二维码
    getClassQr: params => service.get('/throw/m/infoflow/getClassQr', { params })
  },
  payment: {
    // 微信外支付
    pay: params => service.post('/throw/v3/wmpay/pay', params),
    // 微信内支付
    payInWx: params => service.post('/throw/v3/m/wmpay/pay', params),
    // 购买前校验
    payBeforeCheck (data) {
      data = { ...getSearch(), ...data }
      return service.post('/throw/v3/wmpay/payBeforeCheck', data)
    },
    // 微信外检查订单
    checkOrder: params => service.get('/throw/v3/common/checkBought', { params }),
    // 微信内检查订单
    checkOrderInWx: params => service.get('throw/v3/m/common/checkBought', { params })
  },
  userOperator: { // 用户操作
    // 发送验证码
    getPhoneCode: params => service.post('/throw/common/code', params),
    // 绑定手机
    phoneCheckCode: params => {
      params = { ...getSearch(), ...params }
      return service.post('/throw/common/checkCode', params)
    },
    // 获取分组测试信息
    getTestGroup: params => service.post('/throw/abtest/getGroup', params),
    // 获取分组测试信息 V3
    getGroup: params => service.post('/throw/v3/abtest/getGroup', params),
    // 一键复制回传  !tips 需要回传后端
    getQuickCopy: params => service.post('/throw/infoflow/quickCopy', params),
    // 长按二维码回传 !tips 需要回传后端
    pressClassQr: params => service.get('/throw/m/infoflow/pressClassQr', { params }),
    // 秒会结果页分流  /throw/abtest/GuidePage/addKefu
    addKefu: params => service.post('/throw/abtest/GuidePage/addKefu', params),
    // 动态获取小程序链接
    getMiniHref: data => service.post('/throw/miniProgram/getMiniProgramUrl', data),
    // 获取最近购买用户
    getLatestUser: (params) => service.get('/throw/infoflow/latestPayUser', { params }),
    // 检查活动是否有效
    checkActive: params => service.get('/throw/activity/checkActive', { params }),
    // 检查用户是否可以参与活动
    checkActiveUser: params => service.get('/throw/active/checkActiveUser', { params }),
    // 零元报名 /api
    signUp: data => service.post('/throw/active/signUp', data),
    // 获取活动二维码  /api
    getQrCode: params => service.get('/throw/active/getQrCode', { params }),
    // 支付按钮回传
    report: data => {
      data = { ...getSearch(), ...data }
      return service.post('/throw/release/report/H', data)
    },
    // 页面访问时长回传
    reportTime: data => service.post('throw/release/report/J', data),
    // 生成长轻海报
    createPoster: data => service.post('/throw/tool/activePage', data),
    // 微信外使用手机号查看分班
    getLastOrderInfo: params => service.get('throw/infoflow/getLastOrderInfo', { params })
  },

  // 大礼包
  giftBox: {
    queryUserAddress: params => service.post('/throw/infoflow/queryUserAddress', params),
    addUserAddress: params => service.post('/throw/infoflow/addUserAddress', params),
    // 微信外检查订单
    checkOrder: params => service.get('/throw/v2/common/checkBought', { params }),
    // 微信外支付
    pay: params => service.post('/throw/v2/wmpay/pay', params)
  },

  // 投放抖音涨粉落地页
  Tiktok: {
    // 无订单一键复制
    quickCopyNoOrderNum: params => service.post('/throw/infoflow/quickCopyNoOrderNum', params),
    copy: params => service.post('/throw/v3/infoflow/quickCopyNoOrderNum', params)
  },
  // 问卷
  question: {
    // 获取问题列表
    getQuestionList: params => service.post('/throw/m/questionnaire/question', params),
    // 提交问卷
    postAnswer: params => service.post('throw/m/questionnaire/answer', params)
  }
}
