import { Toast } from 'vant'

/**
 * 获取当前环境下px对应的rem值
 * @param {*} pxNum
 * @returns rem
 */
export function px2rem (pxNum) {
  return `${pxNum / 75}rem`
}

/**
 * 是否为微信环境
 * @returns
 */
export function isWeiXin () {
  return navigator.userAgent.toLowerCase().indexOf('micromessenger') !== -1
}

/**
 * 获取url query
 * @export
 * @param {*} getQuery
 * @returns
 */
export function getQuery (key) {
  const reg = new RegExp(`(^|&)${key}=([^&]*)(&|$)`, 'i')
  const r = window.location.search.substr(1).match(reg)
  return r ? unescape(r[2]) : null
}

/**
 * obj转 url query
 * @param {*} Object
 */
export function obj2str (object) {
  return (
    '?' +
    Object.keys(object)
      .map((key) => `${key}=${object[key]}`)
      .join('&')
  )
}

// 设置导航栏标题
export function setDocumentTitle (title) {
  try {
    const document = window.document
    document.title = title
    if (/ip(hone|od|ad)/i.test(navigator.userAgent)) {
      const i = document.createElement('iframe')
      i.src = '/replace_title'
      i.style.display = 'none'
      i.onload = () => {
        setTimeout(() => {
          i.remove()
        }, 9)
      }
      document.body.appendChild(i)
    }
  } catch (err) {}
}

// 判断当前为是否IOS
export function isIOS () {
  const u = navigator.userAgent
  const isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
  return isiOS
}

// 判断当前是否为安卓
export function isAndroid () {
  const u = navigator.userAgent
  return u.indexOf('Android') > -1 || u.indexOf('Linux') > -1
}

// 判断当前是否为pc端微信
export function isPcWechat () {
  const u = navigator.userAgent
  return u.includes('Wechat')
}

// 获取当前操作系统
export function getOS () {
  var os
  if (
    navigator.userAgent.indexOf('Android') > -1 ||
    navigator.userAgent.indexOf('Linux') > -1
  ) {
    os = 'Android'
  } else if (
    navigator.userAgent.indexOf('iPhone') > -1 ||
    navigator.userAgent.indexOf('iPad') > -1
  ) {
    os = 'iOS'
  } else if (navigator.userAgent.indexOf('Windows Phone') > -1) {
    os = 'WP'
  } else {
    os = 'Others'
  }
  window.localStorage.setItem('os', os)
  return os
}

/**
 * @description 复制文本
 * @param {String} value 需要复制的文本
 */
export function copyText (value, toastMsg = '复制成功') {
  const inputEl = document.createElement('input')
  inputEl.setAttribute('readonly', 'readonly')
  inputEl.value = value
  document.body.appendChild(inputEl)
  try {
    inputEl.select()
    document.execCommand('copy')
    Toast.success(toastMsg)
  } catch (error) {
    Toast.fail('复制失败，请手动复制')
  }
  document.body.removeChild(inputEl)
}

/**
 * @description: 生成min-max的随机数
 * @param {number} min
 * @return {number} max
 */
export const intervalRandom = function (min, max) {
  const range = max - min
  const rand = Math.random()
  const num = min + Math.round(rand * range)

  return num
}

/**
 * @description: 滚动到顶部动画
 * @param {Number} duration 滚动总时间
 * @return {Boolean} interval 滚动频率
 */
export const scrollTopAnimi = function (duration = 300, interval = 10) {
  let curTop = document.documentElement.scrollTop || document.body.scrollTop // 滑动总距离
  const step = Math.round((curTop / duration) * interval) // 每次走的距离
  const timer = window.setInterval(function () {
    if (curTop <= 0) {
      window.clearInterval(timer)
      return
    }
    curTop -= step
    document.documentElement.scrollTop = document.body.scrollTop = curTop
  }, interval)
}

/**
 * @Date: 2021-01-20 12:06:25
 * @param {*} key
 * @description: 取#前后的参数
 */
export function getSearch (key) {
  const { protocol, host, hash, search, pathname } = location
  const queryStr = search.substr(1) + '&' + (hash.split('?')[1] || '')
  const query = {}
  queryStr &&
    queryStr.split('&').forEach((item) => {
      if (!item) return
      const tmp = item.split('=')
      query[tmp[0]] = tmp[1]
    })
  if (key) return query[key]
  // 后端回传需要特殊增加参数
  return {
    protocol,
    host,
    link: pathname + search + hash.split('&wm_click_id')[0],
    ...query
  }
}
/**
 * @Date: 2021-01-08 19:22:25
 * @author: zw
 * @description: 手机号验证
 */
export function isPhone (phone) {
  return /^[1]([3-9])[0-9]{9}$/.test(phone)
}
export const phoneReg = /^(?:(?:\+|00)86)?1[3-9]\d{9}$/
// 1维->2维
export function calArray (baseArray, n) {
  const len = baseArray.length
  const lineNum = len % n === 0 ? len / n : Math.floor(len / n + 1)
  const res = []
  for (let i = 0; i < lineNum; i++) {
    const temp = baseArray.slice(i * n, i * n + n)
    res.push(temp)
  }
  return res
}
export function goAnchor (selector, toTop = 350) {
  // 移动距离
  let top = 0
  // 当前滚动条位置
  const scrollTop =
    document.documentElement.scrollTop ||
    window.pageYOffset ||
    document.body.scrollTop
  if (typeof selector === 'number') {
    top = selector - scrollTop
  } else {
    const anchor = document.querySelector(selector) || { offsetTop: 0 }
    top = anchor.offsetTop - scrollTop
  }
  window.scrollBy({ top: top + toTop, behavior: 'smooth' })
}
