import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import { sync } from 'vuex-router-sync'

// 接口
import WmApi from './api'

// 微信
import promiseWx from '@/libs/promiseWx'

// vant ui
import registerVant from './libs/vant'

// 公用样式
import '@/scss/index.scss'

// Vconsole
import '@/libs/vconsole'

// setTitle
import { setDocumentTitle, getOS, phoneReg } from '@/utils'

// 埋点
import { registerLog } from '@/libs/log'

// 全局组件 modal
import BaseModal from '@/components/BaseModal'

// 获取设备信息
getOS()

registerLog(Vue, store)
registerVant(Vue)
sync(store, router)
registerVant(Vue)

Vue.use(WmApi)
Vue.use(promiseWx)
Vue.prototype.$setTitle = setDocumentTitle
Vue.prototype.baseUrl = '//wm-front-common.weimiaocaishang.com/wm-front-landing-page-mobile-custom/assets/'
Vue.prototype.phoneReg = phoneReg
Vue.component('BaseModal', BaseModal)

store.$apis = WmApi

Vue.config.productionTip = false
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
