export default {
  namespaced: true,
  state: {
    productSn: 'PRODUCT_SN_XIAOBAI_29', // 大礼包 商品编码
    link: '' // link
  },
  mutations: {
    SET_LINK (state, payload) {
      state.link = payload
    }
  },
  actions: {
    /**
     * @description 获取进入页面地址
     */
    async setLink ({ commit }, link) {
      commit('SET_LINK', link)
    }
  }
}
